<template>
  <div>
    <div class="text-center ma-4"></div>
    <div>
      <video :width="windowWidth" autoplay muted>
        <source src="../../assets/artistica.mp4" type="video/mp4" />
      </video>
    </div>
    <v-container class="pt-6">
      <div>
        <!-- <div
