            <span class="font-600">Ver todos los productos</span>
          </a>
        </div>
        <div v-for="(n, index) in getCategory" :key="index">
          <a
            text
            @click="idCategorie = n._id"
            class="px-6 py-2 home-two-sidebar-link"
          >
            <v-avatar size="24" tile class="me-3">
              <img
                v-svg-inline
                class="icon mr-2"
