            </v-avatar>
            <span class="font-600">{{ n.nombre }}</span>
          </a>
        </div>
      </div> -->
        <div class="box-content">
          <!-- <div class="d-flex justify-end pa-2 d-block d-md-none"> -->
          <!-- <div>
            <v-btn icon @click="isSidebar = !isSidebar">
              <v-icon dark> mdi-format-list-bulleted-square </v-icon>
            </v-btn>
          </div> -->
          <div>
            <v-row>
              <v-col cols="12" class="pb-0">
                <h2 class="mb-1 grey--text text--darken-4">Productoss</h2>
              </v-col>
              <v-col cols="12" class="mb-8">
                <v-row v-if="idCategorie == '0' && getTextSearch.length == 0">
                  <v-col
                    cols="12"
                    lg="4"
                    v-for="(item, index) in getProducts"
                    :key="index"
                  >
                    <CardCart
                      :content-img="item.img"
                      :content-text="item.nombre"
                      :content-description="item.descripcion"
                      :counter="item.cantidadInit"
                      :content-price="item.precio"
                      class="m-5"
                      @cartRemove="removeCart(item)"
                      @cartAdd="addCart(item)"
                    />
                  </v-col>
                </v-row>
                <v-row v-if="idCategorie != '0' && getTextSearch.length == 0">
                  <v-col
                    cols="12"
                    lg="4"
                    v-for="(item, index) in getProducts.filter(
                      (e) => e.categoria._id == idCategorie
                    )"
                    :key="index"
                  >
                    <CardCart
                      :content-img="item.img"
                      :content-text="item.nombre"
                      :content-description="item.descripcion"
                      :counter="item.cantidadInit"
                      :content-price="item.precio"
                      class="m-5"
                      @cartRemove="removeCart(item)"
                      @cartAdd="addCart(item)"
                    />
                  </v-col>
                </v-row>
                <v-row v-if="idCategorie == '0' && getTextSearch.length > 0">
                  sad
                  <v-col
                    cols="12"
                    lg="4"
                    v-for="(item, index) in getProducts.filter((e) =>
                      e.nombre
                        .toUpperCase()
                        .includes(getTextSearch.toUpperCase())
                    )"
                    :key="index"
                  >
                    <CardCart
                      :content-img="item.img"
                      :content-text="item.nombre"
                      :content-description="item.descripcion"
                      :counter="item.cantidadInit"
                      :content-price="item.precio"
                      class="m-5"
                      @cartRemove="removeCart(item)"
                      @cartAdd="addCart(item)"
                    />
                  </v-col>
                </v-row>
                <v-row v-if="idCategorie != '0' && getTextSearch.length > 0">
                  <v-col
                    cols="12"
                    lg="4"
                    v-for="(item, index) in getProducts.filter(
                      (e) =>
                        e.categoria._id == idCategorie &&
                        e.nombre
                          .toUpperCase()
                          .includes(getTextSearch.toUpperCase())
                    )"
                    :key="index"
                  >
                    <CardCart
                      :content-img="item.img"
                      :content-text="item.nombre"
                      :content-description="item.descripcion"
                      :counter="item.cantidadInit"
                      :content-price="item.precio"
                      class="m-5"
                      @cartRemove="removeCart(item)"
