                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </div>
        </div>

        <div
          class="box-sidebar flex-column home-two-sidebar  d-md-block d-none"
          :class="{ open: isSidebar }"
        >
            <!-- cartCount  -->
            <v-badge
              bordered
              color="error"
              :content="
                getCartProducts.length == 0 ? '0' : getCartProducts.length
              "
              overlap
            >
              <v-btn
                @click="drawer = true"
                elevation="0"
                fab
                color="grey lighten-2"
                big
              >
                <v-icon color="">mdi-cart</v-icon>
              </v-btn>
            </v-badge>

        </div>
      </div>
    </v-container>
<drawerNav :visible="drawer" @close="drawer = false" />
  </div>
</template>

<script>
// import videoPlayer from 'vuetify-media-player/src/components/video-player.vue'
import CardCart from "@/components/cartCard/CardCart";
import { mapGetters, mapActions } from "vuex";
import drawerNav from "../../components/DrawerNav.vue";
export default {
  components: {
    CardCart,
    drawerNav
  },
  computed: {
    ...mapGetters([
      "getProducts",
      "getCategory",
      "getTextSearch",
      "getCartProducts",
    ]),
  },
  data() {
    return {
      drawer: false,
      windowWidth: window.innerWidth,
      idCategorie: "0",
      isSidebar: false,
      isSidebarHomeTwo: false,
      slickSettingsSingleItem: {
        infinite: false,
        arrows: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: 0,
      },
      icons: [
        "mdi-facebook",
        "mdi-twitter",
        "mdi-youtube",
        "mdi-google",
        "mdi-instagram",
      ],
      slickSettingsFour: {
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        initialSlide: 0,

        responsive: [
          {
            breakpoint: 1904,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
            },
          },
          {
            breakpoint: 1263,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              infinite: true,
            },
          },
          {
            breakpoint: 960,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
            },
          },
        ],
      },
    };
  },
  created() {
    this.$http
      .get("/categorias/allcategorias/648f0ce30d6589985ab29881")
      .then((result) => {
        this.addCategories(result.data.categorias);
      });
    this.$http
      .get("/productos/allproductos/648f0ce30d6589985ab29881")
      .then((result) => {
        this.addProducts(result.data.productos);
      });
    window.addEventListener("scroll", this.handleScroll);
  },

  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  mounted() {
    // this.getCategories();
    window.onresize = () => {
      this.windowWidth = window.innerWidth;
    };
  },
  methods: {
    ...mapActions(["addCategories", "addProducts", "addCart", "removeCart"]),
    handleScroll() {
      const scrolled = window.scrollY;
      let HomeTwoSidebar = document.querySelector(".home-two-sidebar");
      if (scrolled > 68) {
        HomeTwoSidebar.classList.add("scroll-home-two");
      } else {
        HomeTwoSidebar.classList.remove("scroll-home-two");
      }
    },
  },
};
</script>

<style lang="scss" scoped>

.quote-open {
  margin-top: -39px;
}
.quote-open-bottom {
  margin-top: 65px;
}
a.footer-link-hover {
  color: #bdbdbd !important;
  &:hover {
    color: #fff !important;
  }
}

.box-sidebar {
  &.home-two-sidebar {
    position: absolute;
    padding-top: 0px;
    height: 0vh;
    @media (max-width: 959px) {
      position: fixed;
      top: 0;
    }
    &.scroll-home-two {
      position: fixed;
      top: 90%;
      left: 90%;
      @media (max-width: 959px) {
        position: fixed;
        top: 100px;
      }
    }
  }
}

.home-two-color {
  color: rgb(75, 86, 107);
}
.home-two-sidebar-link {
  color: rgb(75, 86, 107);
  &:hover {
    color: #d23f57;
  }
}
</style>
